    /*background-color: black;*/
    font-family: 'Gotham Narrow';
}

* {
    letter-spacing: 0;

    color: #ffffff;
    font-weight: 200;
}

.menuContainer {
    position: absolute;
    margin: 0;
    top: 0;
    left: 0;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
}

.menu {
    position:relative;

    background-color: #202025;
    padding: 0;
    margin-left: 20px;

    border-radius: 5px;

    max-width: 100%;
    max-height: 40%;

    overflow-y: scroll;
}

/* Scrollbar */
::-webkit-scrollbar {
    position: relative;
    width: 0px;
}

.menu .title {
    font-size: 17px;
    font-weight: 300;
    padding: 13px 150px 13px 13px;
    border-bottom: 1px solid #434449;
}

.menu li {
    display: flex;
    align-items: center;

    list-style: none;
    user-select: none;

    font-size: 17px;

    width: 300px;

    padding: 15px 0px 15px 13px;
    transition: 0.2s ease-in-out;
}

.pointer {
    cursor: pointer;
}

.content {
    display: flex;
    justify-content: space-between;

    width: 100%;
}

.slider {
    display: flex;
    margin-right: 10px;
}

.slider input {
    background-color: transparent;
    outline: none;
    border: none;

    width: 30px;
    font-size: 14px;
    text-align: center;
}

.slider svg {
    width: 15px;
    opacity: 0.5;
}

.error {
    animation: error 0.7s forwards;
}

@keyframes error {
    0% {color: #e2e2e2;}
    50% {color: #de4848;}
    100% {color: #e2e2e2;}
}

.icon {
    width: 20px;
    margin-right: 10px;
}

.menu li:first-child {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.menu li:last-child {
    border-top: 1px solid #434449;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.menu li:hover {
    transition: 0.2s ease-in-out;
    background-color: #313137;
}

/**/

.dialogContainer {
    position: absolute;
    margin: 0;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.5);

    pointer-events: none;
}

.dialog {
    pointer-events:all;

    font-family: "Bahnschrift";
    
    width: 450px;
    border-radius: 5px;
    padding: 10px 0 20px 0;
    background-color: #202025;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.dialog .title {
    font-size: 30px;
    font-weight: bold;

    margin-top: 10px;
}

.dialog .description {
    font-weight: 100;
    
    margin: 10px 0px;
    opacity: 0.5;
}

.dialog .input {
    width: 93%;
    padding: 10px 0px;
}

.dialog .input .name {
    font-size: 13px;
    font-weight: 300;
    text-transform: uppercase;
    
    opacity: 0.6;
    margin-left: 1px;
}

.dialog .input .text {
    font-weight: 100;
    font-size: 15px;
    
    outline: 0;
    border: none;
    margin: 0;
    box-sizing: border-box;
    
    width: 100%;
    border-radius: 5px;
    padding: 15px 10px;
    background-color: #18181c;
    
    margin-top: 10px;

    opacity: 0.8;
}

.buttons {
    display: flex;
    gap: 10px;

    width: 93%;

    margin-top: 20px;
    user-select: none;
}

.button {
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}

.buttons .cancel {
    background-color: transparent;
}
.buttons .cancel:hover {
    background-color: rgba(255, 255, 255, 0.1);
}
.buttons .done {
    background-color: #0071FF;
}
.buttons .done:hover {
    background-color: #005ed0;
}

.external {
    position: absolute;
    margin: 0;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;

    z-index: -1;
}